import { MY_COURSE_FAIL, MY_COURSE_REQUEST, MY_COURSE_SUCCESS,CLEAR_ERRORS, ADMIN_COURSES_REQUEST, ADMIN_COURSES_SUCCESS, ADMIN_COURSES_FAIL, ALL_COURSES_REQUEST, ALL_COURSES_SUCCESS, ALL_COURSES_FAIL, NEW_COURSES_REQUEST, NEW_COURSES_SUCCESS, NEW_COURSES_FAIL, NEW_COURSES_RESET, COURSE_DETAILS_REQUEST, COURSE_DETAILS_SUCCESS, COURSE_DETAILS_FAIL } from "../constants/CourseConstants";

export const coursesReducer = (state = { courses: [] }, action) => {
  switch (action.type) {
    case ALL_COURSES_REQUEST:
    case ADMIN_COURSES_REQUEST:
      return {
        loading: true,
        courses: [],
      };
    case ALL_COURSES_SUCCESS:
      return {
        loading: false,
        courses: action.payload.courses,
        coursesCount: action.payload.CoursesCount,
        resultPerPage: action.payload.resultPerPage,
        filteredcoursesCount: action.payload.ffilteredcoursesCount,
      };

    case ADMIN_COURSES_SUCCESS:
      return {
        loading: false,
        courses: action.payload,
      };
    case ALL_COURSES_FAIL:
    case ADMIN_COURSES_FAIL:
      return {
        loading: false,
        error: action.payload,
      };

    case CLEAR_ERRORS:
      return {
        ...state,
        error: null,
      };
    default:
      return state;
  }
};



export const myCourseReducer = (state = { courses: [] }, action) => {
    switch (action.type) {
      case MY_COURSE_REQUEST:
        return {
          loading: true,
        };
  
      case MY_COURSE_SUCCESS:
        return {
          loading: false,
          courses: action.payload,
        };
  
      case MY_COURSE_FAIL:
        return {
          loading: false,
          error: action.payload,
        };
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };


  export const newCourseReducer = (state = {course: {} }, action) => {
    switch (action.type) {
      case NEW_COURSES_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case NEW_COURSES_SUCCESS:
        return {
          loading: false,
          success: action.payload.success,
          course: action.payload.course,
        };
      case NEW_COURSES_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
      case NEW_COURSES_RESET:
        return {
          ...state,
          success: false,
        };
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
      default:
        return state;
    }
  };


  export const CourseDetailsReducer = (state = { course: {} }, action) => {
    switch (action.type) {
      case COURSE_DETAILS_REQUEST:
        return {
          loading: true,
          ...state,
        };
      case COURSE_DETAILS_SUCCESS:
        return {
          loading: false,
          course: action.payload,
        };
      case COURSE_DETAILS_FAIL:
        return {
          loading: false,
          error: action.payload,
        };
  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
      default:
        return state;
    }
  };
  