import React from 'react'
import { useSelector } from 'react-redux';

const Profile = ({history}) => {
    const { user } = useSelector(
        (state) => state.user
    );
    if (user.role === "teacher") {
        history.push("/teacherdashboard");
    }
    if (user.role === "admin") {
        history.push("/");
    }
    

    return (
    <>
    
    
    </>
  )
}

export default Profile