import React, { useEffect } from 'react'
import { useAlert } from 'react-alert';
import { useDispatch, useSelector } from 'react-redux';
import { getAllOrders } from '../redux/actions/orderAction';
import { Link } from 'react-router-dom';
import TeacherSidebar from './HomePage/TeacherSidebar';
import Header from '../components/Header';
import { clearErrors, deleteUser, loadUser } from '../redux/actions/userAction';
import { DELETE_USER_RESET } from '../redux/constants/userConstants';

const MyStudents = ({ history }) => {

    const alert = useAlert();
    const dispatch = useDispatch();
    const { error, user } = useSelector(
        (state) => state.user
    );
    const { orders } = useSelector(
        (state) => state.allOrders
    )
    const {
        error: deleteError,
        isDeleted,
        message,
    } = useSelector((state) => state.profile);

    const deleteUserHandler = (id) => {
        dispatch(deleteUser(id));
    };

    useEffect(() => {
        if (error) {
            alert.error(error);
            dispatch(clearErrors());
        }

        if (deleteError) {
            alert.error(deleteError);
            dispatch(clearErrors());
        }

        if (isDeleted) {
            alert.success(message);
            history.push("/allstudents");
            dispatch({ type: DELETE_USER_RESET });
        }


        dispatch(loadUser());

        dispatch(getAllOrders());
    }, [dispatch, alert, error, deleteError, history, isDeleted, message]);
    return (
        <>
            <TeacherSidebar />
            <main className="main-wrap">
                <Header />

                <div class="content container-fluid">
                    <div class="page-header">
                        <div class="row">
                            <div class="col-lg-6 col-md-6 col-sm-6 col-12">
                                <h5 class="text-uppercase mb-0 mt-0 page-title">My Students</h5>
                            </div>
                            <div class="col-lg-6 col-md-6 col-sm-6 col-12">
                                <ul class="breadcrumb float-right p-0 mb-0">
                                    <li class="breadcrumb-item"><a href="index.html"><i class="fas fa-home"></i> Home</a></li>
                                    <li class="breadcrumb-item"><a href="#">Management</a></li>
                                    <li class="breadcrumb-item"><span>mystudents</span></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-4 col-4">
                        </div>
                        <div class="col-sm-8 col-8 text-right add-btn-col">
                        </div>
                    </div>
                    <div class="content-page">
                        <div class="row filter-row">
                            <div class="col-sm-6 col-md-3">
                                <div class="form-group form-focus select-focus">
                                    <select class="form-control select">
                                        <option>Select </option>
                                        {orders && orders
                                            .filter(order => {
                                                return (
                                                    order.teacher === user.name
                                                );
                                            })
                                            .map((order, index) => {
                                                return (
                                                    <>
                                                        <option>{order.user.name}</option>
                                                    </>
                                                );
                                            })}
                                    </select>
                                    <label class="focus-label">My Students</label>
                                </div>
                            </div>
                            <div class="col-sm-6 col-md-3">
                                <div class="form-group form-focus">
                                    <input class="form-control datetimepicker-input datetimepicker floating" type="text" data-toggle="datetimepicker" />
                                    <label class="focus-label">From</label>
                                </div>
                            </div>
                            <div class="col-sm-6 col-md-3">
                                <div class="form-group form-focus">
                                    <input class="form-control datetimepicker-input datetimepicker floating" type="text" data-toggle="datetimepicker" />
                                    <label class="focus-label">To</label>
                                </div>
                            </div>
                            <div class="col-sm-6 col-md-3">
                                <a href="#" class="btn btn-search rounded btn-block mb-3"> search </a>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-12 mb-3">
                                <div class="table-responsive">
                                    <table class="table custom-table datatable">
                                        <thead class="thead-light">
                                            <tr>
                                                <th style={{ width: "30%;" }}>Name</th>
                                                <th>Email</th>
                                                <th>Course</th>
                                                <th>Enroll At</th>
                                                <th>Role</th>
                                                <th class="text-right">Action</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {orders && orders
                                                .filter(order => {
                                                    return (
                                                        order.teacher === user.name
                                                    );
                                                })
                                                .map((order, index) => {
                                                    return (
                                                        <>



                                                            <tr>
                                                                <td>
                                                                    <a href="profile.html" class="avatar"><img src={order.user && order.user.avatar.url} /></a>
                                                                    <h2><Link to={`/userdetails/${user._id}`}><a>{order.user.name}<span>Student</span></a></Link></h2>
                                                                </td>
                                                                <td><a>{order.user && order.user.email}</a></td>
                                                                <td> <Link to={`/userdetails/${user._id}`}><a>{order && order.name}</a></Link></td>
                                                                <td>{String(order && order.createdAt).substr(0, 10)}</td>
                                                                <td>
                                                                    <span class="badge badge-info-border">Student</span>
                                                                    {/* <div className={user&&user.role === 'user' ? 'blue-background': 'default-background'}> <span class="badge ">{user&&user.role}</span></div> */}
                                                                </td>
                                                                <td class="text-right">
                                                                    <div class="dropdown dropdown-action">

                                                                        <div class=" profile-action ">
                                                                            <Link className="dropdown-toggle" data-bs-toggle="dropdown" to="#">
                                                                                <a href="#" class="action-icon dropdown-toggle" data-toggle="dropdown" aria-expanded="false"><i class="fas fa-ellipsis-v"></i></a>
                                                                            </Link>
                                                                            <div className="dropdown-menu dropdown-menu-end">
                                                                            
                                                                           
                                                                            <button className="dropdown-item text-danger" onClick={() =>
                                                                                    deleteUserHandler((user._id))
                                                                                }>
                                                                                Delete
                                                                            </button>
                                                                        </div>

                                                                        </div>

                                                                    </div>

                                                                </td>
                                                            </tr>
                                                        </>
                                                    );
                                                })}

                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>

        </>)
}

export default MyStudents