import React from "react";
import { Link, NavLink } from "react-router-dom";

import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import PostAddIcon from "@material-ui/icons/PostAdd";
import AddIcon from "@material-ui/icons/Add";
import EidtIcon from "@material-ui/icons/Edit";
import TreeView from '@mui/lab/TreeView';
import TreeItem from '@mui/lab/TreeItem';
import ImportExportIcon from "@material-ui/icons/ImportExport";
import ListAltIcon from "@material-ui/icons/ListAlt";
import DashboardIcon from "@material-ui/icons/Dashboard";
import PeopleIcon from "@material-ui/icons/People";
import RateReviewIcon from "@material-ui/icons/RateReview";
import logo from '../img/logo1.png'
const Sidebar = () => {

  return (
    <aside className="navbar-aside" id="offcanvas_aside">
      <div className="aside-top">
        <Link to="/" className="brand-wrap">
          <img
            src={logo}
            style={{ height: "46" }}
            className="logo"
            alt="Ecommerce dashboard template"
          />
        </Link>
        <div>
          <button className="btn btn-icon btn-aside-minimize">
            <i className="text-muted fas fa-stream"></i>
          </button>
        </div>
      </div>

      <nav>
        <ul className="menu-aside">
          <li className="menu-item">
            <NavLink
              activeClassName="active"
              className="menu-link"
              to="/"
              exact={true}
            >
              <i className="icon fas fa-home"></i>
              <span className="text">Dashboard</span>
            </NavLink>
          </li>
          
          <li className="menu-item" style={{ backgroundColor: "transparent" }}>
            <NavLink
              activeClassName="active"
              className="menu-link"
              to="/allteacher"
            >
              <TreeView
                defaultCollapseIcon={<ExpandMoreIcon />}
                defaultExpandIcon={<i className="icon fas fa-user"></i>}
              >
                <TreeItem nodeId="1" label="  Teacher">
                  <NavLink to="/allteacher">
                    <TreeItem nodeId="2" label="All" icon={<PostAddIcon />} />
                  </NavLink>

                  <NavLink to="/addcourses">
                    <TreeItem nodeId="3" label="Create" icon={<AddIcon />} />
                  </NavLink>
                  <NavLink to="/admin/product">
                    <TreeItem nodeId="4" label="Edit" icon={<EidtIcon />} />
                  </NavLink>
                </TreeItem>
              </TreeView>
            </NavLink>
          </li>
        
         
          
          <li className="menu-item">
            <NavLink
              activeClassName="active"
              className="menu-link"
              to="/allcourses"
            >
              <i className="icon icon fas fa fa-book"></i>
              <span className="text">Courses</span>
            </NavLink>
          </li>
          <li className="menu-item">
            <NavLink
              activeClassName="active"
              className="menu-link"
              to="/users"
            >
              <i className="icon fas fa-user"></i>
              <span className="text">Users</span>
            </NavLink>
          </li>
          <li className="menu-item" style={{ backgroundColor: "transparent" }}>
            <NavLink
              activeClassName="active"
              className="menu-link"
              to="/students"
            >
              <TreeView
                defaultCollapseIcon={<ExpandMoreIcon />}
                defaultExpandIcon={<i className="icon fas fa-user"></i>}
              >
                <TreeItem nodeId="1" label="  Students">
                  <NavLink to="/students">
                    <TreeItem nodeId="2" label="All" icon={<PostAddIcon />} />
                  </NavLink>

                  <NavLink to="/admin/product">
                    <TreeItem nodeId="3" label="Create" icon={<AddIcon />} />
                  </NavLink>
                  <NavLink to="/admin/product">
                    <TreeItem nodeId="4" label="Edit" icon={<EidtIcon />} />
                  </NavLink>
                </TreeItem>
              </TreeView>
            </NavLink>
          </li>
          

          
        </ul>
        <br />
        <br />
      </nav>
    </aside>

  );
};

export default Sidebar;
