import { ADMIN_COURSES_FAIL, ADMIN_COURSES_REQUEST, ADMIN_COURSES_SUCCESS, ALL_COURSES_FAIL, ALL_COURSES_REQUEST, ALL_COURSES_SUCCESS, CLEAR_ERRORS, COURSE_DETAILS_FAIL, COURSE_DETAILS_REQUEST, COURSE_DETAILS_SUCCESS, MY_COURSE_FAIL, MY_COURSE_REQUEST, MY_COURSE_SUCCESS, NEW_COURSES_FAIL, NEW_COURSES_REQUEST, NEW_COURSES_SUCCESS } from "../constants/CourseConstants";
import axios from "axios";

export const myOrders = () => async (dispatch) => {
    try {
      dispatch({ type: MY_COURSE_REQUEST });
  
      const { data } = await axios.get("/api/v1/courses/me");
  
      dispatch({ type: MY_COURSE_SUCCESS, payload: data.courses });
    } catch (error) {
      dispatch({
        type: MY_COURSE_FAIL,
        payload: error.response.data.message,
      });
    }
  };

  // Get All Courses For Admin
export const getAdminCourses = () => async (dispatch) => {
  try {
    dispatch({ type: ADMIN_COURSES_REQUEST });

    const { data } = await axios.get("/api/v1/admin/courses");

    dispatch({
      type: ADMIN_COURSES_SUCCESS,
      payload: data.courses,
    });
  } catch (error) {
    dispatch({
      type: ADMIN_COURSES_FAIL,
      payload: error.response.data.message,
    });
  }
};

// Get All Courses
export const getCourses =
  (keyword = "", currentPage = 1, price = [0, 25000], category, ratings = 0) =>
  async (dispatch) => {
    try {
      dispatch({ type: ALL_COURSES_REQUEST });

      let link = `/api/v1/courses?keyword=${keyword}&page=${currentPage}&price[gte]=${price[0]}&price[lte]=${price[1]}&ratings[gte]=${ratings}`;

      if (category) {
        link = `/api/v1/courses?keyword=${keyword}&page=${currentPage}&price[gte]=${price[0]}&price[lte]=${price[1]}&category=${category}&ratings[gte]=${ratings}`;
      }

      const { data } = await axios.get(link);

      dispatch({
        type: ALL_COURSES_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: ALL_COURSES_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  // Create Course
  export const createCourse = (courseData) => async (dispatch) => {
    try {
      dispatch({ type: NEW_COURSES_REQUEST });
  
      const config = {
        headers: { "Content-Type": "application/json" },
      };
  
      const { data } = await axios.post(
        `/api/v1/admin/Course/new`,
        courseData,
        config
      );
  
      dispatch({
        type: NEW_COURSES_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: NEW_COURSES_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  // Get Course Details
export const getCourseDetails = (id) => async (dispatch) => {
  try {
    dispatch({ type: COURSE_DETAILS_REQUEST });

    const { data } = await axios.get(`/api/v1/course/${id}`);

    dispatch({
      type: COURSE_DETAILS_SUCCESS,
      payload: data.course,
    });
  } catch (error) {
    dispatch({
      type: COURSE_DETAILS_FAIL,
      payload: error.response.data.message,
    });
  }
};


// Clearing Errors
export const clearErrors = () => async (dispatch) => {
  dispatch({ type: CLEAR_ERRORS });
};