import React, { Fragment, useEffect } from 'react'
import { useAlert } from 'react-alert';
import { useDispatch, useSelector } from 'react-redux';
import { getCourses } from '../redux/actions/CourseAction';
import { Link, useParams } from 'react-router-dom';
import TeacherSidebar from './HomePage/TeacherSidebar';
import Header from '../components/Header';
const Mycourses = () => {
    const dispatch = useDispatch();
    const alert = useAlert()
    const { user, loading, isAuthenticated } = useSelector((state) => state.user);

    const {
        courses,

        coursesCount,
        resultPerPage,
        filteredcoursesCount,
    } = useSelector((state) => state.allcourses);
    useEffect(() => {
        dispatch(getCourses());

    }, [dispatch]);



  return (
<>
<TeacherSidebar/>
<main className="main-wrap">
                <Header />
                <div className='row'>
                                                {courses && courses
                                                    .filter(Course => {
                                                        return (
                                                            Course.user._id === user._id
                                                        );
                                                    })
                                                    .map((course, index) => {
                                                        return (




                                                            <div class="col-lg-4 col-md-6">
                                                                
                                                                <div class="yl-popular-course-img-text">
                                                                    <div class="yl-popular-course-img text-center">
                                                                        
                                                                        <img src={course.images[0] && course.images[0].url} alt="" />
                                                                    </div>
                                                                    <div class="yl-popular-course-text">
                                                                        <div class="popular-course-fee clearfix">
                                                                            <span>Course Fee:  </span>
                                                                            <div class="course-fee-amount">
                                                                                <del>$59</del>
                                                                                <strong>$39</strong>
                                                                            </div>
                                                                        </div>
                                                                        <div class="popular-course-title yl-headline">
                                                                            <h3><Link to={`/coursedetail/${course._id}`}><a>{course.name}</a></Link>
                                                                            </h3>
                                                                            <div class="yl-course-meta">
                                                                                <a href="#"><i class="fas fa-file"></i>14 Lessons</a>
                                                                                <a href="#"><i class="fas fa-user"></i> 20 Students</a>
                                                                            </div>
                                                                        </div>
                                                                        <div class="popular-course-rate clearfix ul-li">
                                                                            <div class="p-rate-vote float-left">
                                                                                <ul>
                                                                                    <li><i class="fas fa-star"></i></li>
                                                                                    <li><i class="fas fa-star"></i></li>
                                                                                    <li><i class="fas fa-star"></i></li>
                                                                                    <li><i class="fas fa-star"></i></li>
                                                                                    <li><i class="fas fa-star"></i></li>
                                                                                </ul>
                                                                                <span>(12 Votes)</span>
                                                                            </div>
                                                                            <div class="p-course-btn ">
                                                                                <a href="#"><i class="fas fa-chevron-right"></i></a>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>

                                                        );
                                                    })}



                                            </div>




                </main>

</>  )
}

export default Mycourses