import React, { useEffect, useState } from "react";
import "./App.css";
import "./responsive.css";
import "react-toastify/dist/ReactToastify.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import HomeScreen from "./screens/HomeScreen";
import ProductScreen from "./screens/productScreen";
import CategoriesScreen from "./screens/CategoriesScreen";
import OrderScreen from "./screens/OrderScreen";
import OrderDetailScreen from "./screens/OrderDetailScreen";
import AddProduct from "./screens/AddProduct";
import Login from "./screens/LoginScreen";
import UsersScreen from "./screens/UsersScreen";
import ProductEditScreen from "./screens/ProductEditScreen";
import NotFound from "./screens/NotFound";
import { useSelector } from "react-redux";
import axios from "axios";
import { loadUser } from "./actions/userAction";
import store from "./store";
import {Navigate} from "react-router-dom";
import AddCourses from "./Courses/AddCourses";
import AllTeacher from "./Teacher/AllTeacher";
import Teacherdetail from "./Teacher/Teacherdetail";
import PrivateRoute from "./PrivateRoute";
import Homepage from "./TeacherDashboard.js/HomePage/Homepage";
import Profile from "./Profile";
import TeacherProfile from "./TeacherDashboard.js/HomePage/TeacherProfile";
import Mycourses from "./TeacherDashboard.js/Mycourses";
import AllStudents from "./TeacherDashboard.js/AllStudents";
import StudentProfile from "./TeacherDashboard.js/StudentProfile";
import AllCourses from "./Teacher/AllCourses";
import Allusers from "./screens/Allusers";
import Userdetails from "./components/users/Userdetails";
import MyStudents from "./TeacherDashboard.js/MyStudents";

function App() {
  const { isAuthenticated, user } = useSelector((state) => state.user);
  const [stripeApiKey, setStripeApiKey] = useState("");

  async function getStripeApiKey() {
    const { data } = await axios.get("/api/v1/stripeapikey");

    setStripeApiKey(data.stripeApiKey);
  }
  useEffect(() => {


    store.dispatch(loadUser());

    getStripeApiKey();
  }, []);
  //disable right click
  // window.addEventListener("contextmenu", (e) => e.preventDefault());
  // window.addEventListener("keydown", (e) => {
  //   if (e.keyCode == 123) e.preventDefault();
  //   if (e.ctrlKey && e.shiftKey && e.keyCode === 73) e.preventDefault();
  //   if (e.ctrlKey && e.shiftKey && e.keyCode === 74) e.preventDefault();
  // });
  return (
    <>
      <Router>

        <Switch>
          {/* <Route exact path="/" element={<HomeScreen />} /> */}
          {/* <Route exact path="/" element={isAuthenticated ? <HomeScreen/> : <Navigate to="/login"  />}/> */}
          <Route exact path="/products" element={<ProductScreen />} />
          {/* <Route exact path="/" component={HomeScreen} /> */}
          <Route exact path="/myprofile" component={TeacherProfile} />
          {/* //Teacher Routes */}
          <Route exact path="/teachercourses" component={Mycourses} />
          <Route exact path="/addcourses" component={AddCourses} />
          <Route exact path="/allstudents" component={AllStudents} />
          <Route exact path="/users" component={Allusers} />
          <Route exact path="/userdetails/:id" component={Userdetails} />
          <Route exact path="/studentdetail/:id" component={StudentProfile} />
          <Route exact path="/mystudents" component={MyStudents} />

          <PrivateRoute
         isAdmin={true}
          exact
          path="/"
          component={HomeScreen}
        />
        <Route exact path="/profile" component={Profile}/>
          <Route
         isTeacher={true}
          exact
          path="/teacherdashboard"
          component={Homepage}
        /> 
                  <Route exact path="/login" component={Login} />
                  <Route exact path="/allteacher" component={AllTeacher} />
                  <Route exact path="/instructordetail/:id" component={Teacherdetail} />

                  <Route exact path="/students" component={UsersScreen} />
                  <Route exact path="/allcourses" component={AllCourses} />

          <Route path="/category" element={<CategoriesScreen/>} />
          <Route path="/orders" element={<OrderScreen/>} />
          <Route path="/order/:id" element={<OrderDetailScreen/>} />
          <Route path="/addproduct" element={<AddProduct/>} />
          <Route path="/students" element={<UsersScreen/>} />
          <Route path="/allteacher" element={<AllTeacher/>} />
          <Route path="/instructordetail/:id" element={<Teacherdetail/>} />
          <Route path="/addcourses" element={<AddCourses/>} />
          <Route path="/product/:id" element={<ProductEditScreen/>} />
          <Route path="/login" element={<Login/>} />
          <Route path="*" element={<NotFound/>} />
        </Switch>

      </Router>
    </>
  );
}

export default App;
