import React, { Fragment, useEffect } from 'react'
import { useAlert } from 'react-alert';
import { useDispatch, useSelector } from 'react-redux';
import { getUserDetails } from '../../redux/actions/userAction';
import { Link } from 'react-router-dom'
import Header from '../Header';
import Sidebar from '../sidebar';
const Userdetails = ({match}) => {
    const dispatch = useDispatch();
    const alert = useAlert()
    const { user, loading, error } = useSelector(
        (state) => state.userDetails
    );
    
    useEffect(() => {
       

        dispatch(getUserDetails(match.params.id));
    }, [dispatch, match.params.id]);


    return (
        <>
        <Sidebar/>
<main className="main-wrap">
                <Header />
            <div >
                <div class="content container-fluid">
                    <div class="page-header">
                        <div class="row">
                            <div class="col-lg-6 col-md-6 col-sm-6 col-12">
                                <h5 class="text-uppercase mb-0 mt-0 page-title">my Profile</h5>
                            </div>
                            <div class="col-lg-6 col-md-6 col-sm-6 col-12">
                                <ul class="breadcrumb float-right p-0 mb-0">
                                    <li class="breadcrumb-item"><a href="index.html"><i class="fas fa-home"></i> Home</a></li>
                                    <li class="breadcrumb-item"><a href="#">Pages</a></li>
                                    <li class="breadcrumb-item"><span> Profile</span></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div class="card-box m-b-0">
                        <div class="row">
                            <div class="col-md-12">
                                <div class="profile-view">
                                    <div class="profile-img-wrap">
                                        <div class="profile-img">
                                            <a href=""><img class="avatar" src={user.avatar&&user.avatar.url} alt=""/></a>
                                        </div>
                                    </div>
                                    <div class="profile-basic">
                                        <div class="row">
                                            <div class="col-md-5">
                                                <div class="profile-info-left">
                                                    <h3 class="user-name m-t-0">{user&&user.name}</h3>
                                                    <h5 class="company-role m-t-0 m-b-0">Preschool</h5>
                                                    <small class="text-muted">{user&&user.designation}</small>
                                                    <div class="staff-id">Employee ID : HOD-0001</div>
                                                </div>
                                            </div>
                                            <div class="col-md-7">
                                                <ul class="personal-info">
                                                    <li>
                                                        <span class="title">Phone: </span>
                                                        <span class="text"><a href="">{user&&user.phoneno}</a></span>
                                                    </li>
                                                    <li>
                                                        <span class="title">Email:</span>
                                                        <span class="text"><a href=""><span class="__cf_email__" data-cfemail="422827242427303b2f352d2c2502273a232f322e276c212d2f">{user&&user.email}</span></a></span>
                                                    </li>
                                                    <li>
                                                        <span class="title">Birthday:</span>
                                                        <span class="text">2nd August</span>
                                                    </li>
                                                    <li>
                                                        <span class="title">Address:</span>
                                                        <span class="text">5754 Airport Rd, Rahim Yar Khan</span>
                                                    </li>
                                                    <li>
                                                        <span class="title">Gender:</span>
                                                        <span class="text">Male</span>
                                                    </li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
</main>

        </>)
}

export default Userdetails