import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, login, logout } from "../actions/userAction";
import logo from '../img/logo.png'
const Login = ({ location, history }) => {
  const dispatch = useDispatch();
  const alert = useAlert();

  const { error, loading, isAuthenticated } = useSelector(
    (state) => state.user
  );
  const [loginEmail, setLoginEmail] = useState("");
  const [loginPassword, setLoginPassword] = useState("");
  const [user, setUser] = useState({
    name: "",
    email: "",
    password: "",
  });
  const { name, email, password } = user;
  const [avatar, setAvatar] = useState("/Profile.png");
  const [avatarPreview, setAvatarPreview] = useState("/Profile.png");


  const loginSubmit = (e) => {
    e.preventDefault();
    dispatch(login(loginEmail, loginPassword));
  };
  function logoutUser() {
    dispatch(logout());
    alert.success("Logout Successfully");
  }
  useEffect(() => {
    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    if (isAuthenticated) {
      history.push("/profile");
    }
  }, [dispatch, error, alert, history, isAuthenticated]);



  return (
    <>

      <div class="main-wrapper">
        <div class="account-page" >
          <div class="container" >
            <h3 class="account-title" style={{color:"black",fontWeight:"600"}}>Login</h3>
            <div class="account-box">
              <div class="account-wrapper">
                <div class="account-logo">
                  <a href="index.html"><img src={logo} alt="SchoolAdmin" /></a>
                </div>
                <form onSubmit={loginSubmit}>
                  <div class="form-group">
                    <label>Username or Email</label>
                    <input className="form-control"
                      placeholder="Email"
                      required
                      value={loginEmail}
                      onChange={(e) => setLoginEmail(e.target.value)} />
                  </div>
                  <div class="form-group">
                    <label>Password</label>
                    <input className="form-control"
                      placeholder="Password"
                      required
                      value={loginPassword}
                      onChange={(e) => setLoginPassword(e.target.value)} />
                  </div>
                  <div class="form-group text-center custom-mt-form-group">
                    <button class="btn btn-primary btn-block account-btn" type="submit" value="Login">Login</button>
                  </div>
                  <div class="text-center">
                    <a href="forgot-password.html">Forgot your password?</a>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;
